@import './../../styles/utils/vars';
@import './../../styles/utils/mixins';
@import './../../styles/utils/activeLink';
@import './../../styles/utils/icons';

.nav {
  height: $small-nav-height;
  width: 100%;
  display: flex;
  align-items: center;
  box-sizing: border-box;
  box-shadow: 0 1px 0 0 $shadow-color;
  position: fixed;
  z-index: 3;
  background-color: $bg-color;

  @include on-desktop {
    height: $big-nav-height;
  }

  &__leftBlock {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;

    @include on-tablet {
      width: auto;
    }
  }

  &__rightBlock {
    display: none;
    justify-content: space-between;

    @include on-tablet {
      display: flex;
      width: 100%;
    }

    &--left {
      gap: 32px;
      height: 48px;
      margin-left: 16px;

      @include on-tablet {
        display: flex;
      }

      @include on-desktop {
        margin-left: 24px;
        gap: 64px;
        height: 64px;
      }
    }

    &--right {
      display: flex;
      align-items: center;
    }
  }
}

.burger {
  display: block;
  width: 100%;
  height: 100%;

  &__leftBlock {
    box-shadow: 0 1px 0 0 $shadow-color;
  }

  &__rightBlock {
    width: 100%;
    height: calc(100% - $small-nav-height - 24px);
    display: flex;
    justify-content: space-between;
    flex-direction: column;
    padding-top: 24px;

    &--left {
      height: 100%;
      display: flex;
      flex-direction: column;
      align-items: center;
      gap: 16px;
      margin: 0;
    }

    &--right {
      border: 1px solid $shadow-color;
      box-sizing: border-box;
    }
  }
}
